.site-list a {
  @media (max-width: $vp-1023) {
    font-size: 14px;
    line-height: 17px;
  }

  @media (max-width: $vp-767) {
    position: relative;
    display: block;
    padding: 15px 40px 13px 25px;
    font-size: 18px;
    line-height: 22px;
    color: $color-font-dark;

    &:hover,
    &:focus {
      color: $color-hover;
    }
  }
}

.site-list {
  margin-top: -5px;

  @media (max-width: $vp-767) {
    display: none;
    margin-top: 0;
  }
}

.site-list.is-active {
  @media (max-width: $vp-767) {
    display: block;
    background-color: $color-font;
    padding: 70px 0 20px;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    width: 89%;

    &::before {
      content: "";
      width: 42px;
      height: 22px;
      background-image: url("../img/svg/logo-dark.svg");
      background-repeat: no-repeat;
      position: absolute;
      top: 20px;
      left: 15px;
    }
  }
}

.site-list.nojs {
  position: static;
  display: block;
  background-color: $color-font;
  padding: 75px 30px 20px 15px;
  margin-left: auto;
  width: 98%;
}

.site-list__item {
  margin-top: 6px;

  @media (max-width: $vp-767) {
    margin-right: 0;
    margin-bottom: 0;
    margin-top: 0;
  }
}
